
.form-field-readonly.bool-text,
table tbody tr td.bool-text {
  text-transform: capitalize;
}


.card-view {
  display: flex;
  align-content: stretch;
  overflow-x: visible;
  -ms-flex-flow: row wrap;
  -moz-flex-flow: row wrap;
  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;
  -moz-justify-content: flex-start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  padding: 4px 12px;
  min-height: 100%;

  .card-item {
    position: relative;
    min-width: 150px;
    margin: 4px;
    padding: 8px;

    background-color: #fff;

    -ms-flex: 1 1 300px;
    -moz-flex: 1 1 300px;
    -webkit-box-flex: 1;
    -webkit-flex: 1 1 300px;
    flex: 1 0 300px;

    .card-image {
      text-align: center;
      float: left;
      width: 64px;

      img {
        max-height: 64px;
        max-width: 64px;
      }
    }

    .card-details {
      width: 100%;
      padding-left: 72px;

      ul {
        list-style: none;
        padding: 0;
      }
    }

    &:before, &:after {
      width: 0;
      height: 0;
      content: " ";
      display: table;
    }

    &.card-link {
      cursor: pointer;
    }

    &.card-ghost {
      height: 0;
      min-height: 0 !important;
      visibility: hidden;
      margin-top: 0;
      margin-bottom: 0;
      padding: 0;
    }
  }
}

.text-overflow {
  display: inline-block;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: top;
}

.data-panel {
  tr.group-header {
    background-image: linear-gradient(to bottom, #fdfdfd, #e5e5e5);
    font-weight: 600;

    td {
      border-left: none;
      border-right: none;
      background-color: transparent;
    }
  }

  section.form-group {
    padding-bottom: 4px;
    margin-bottom: 0;
  }
}

#loading-msg {
  position: fixed;
  border-radius: 2px;
  top: 0;
  right: 50%;
  opacity: .9;
  color: #2a2a2a;
  text-align: center;
  padding: 4px 12px;
  z-index: 9999;
  background-color: #f7f7f7;
  -webkit-box-shadow: 2px 4px 9px -1px rgba(0,0,0,0.75);
  -moz-box-shadow: 2px 4px 9px -1px rgba(0,0,0,0.75);
  box-shadow: 2px 4px 9px -1px rgba(0,0,0,0.75);
}


.btn-voice-command.active {
  color: red !important;
}

td.DecimalField, th.DecimalField {
  text-align: right;
}

th span {
  cursor: default;
  text-transform: uppercase;
}

.page-badge-ref {
  position: absolute;
  right: 50px;
  top: 20px;
}

.modal {
  z-index: 9999;
}

.tab-pane {
  padding-top: 10px;
}

.nav-tabs {
  border-bottom: 1px solid #ddd;
}

section div.checkbox {
  margin-top: 6px;
  margin-bottom: 6px;
}

.pagination-area {
  display: inline-block;
  margin: 0 6px 0 0;
}


.form-label {
  font-weight: 600;
}

.form-field-readonly {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  min-height: 26px;
  border-bottom: 1px solid #e4e4e4;
}

.table > tbody > tr > td.list-column-delete,
.table > thead > tr > th.list-column-delete {
  padding: 5px 10px;
  text-align: center;
  width: 1px;
}

.table > tbody > tr {
  cursor: pointer;
  background-color: #ffffff;
}

label.checkbox {
  margin: 0;
}

section.form-group {
  //min-height: 59px;
}

input[type=text].form-field:required {
  background-color: #eaebff;
}

input[type=text].form-field.ng-dirty {
  background-color: #fffdf1;
}

input[type=text].form-field.ng-touched.ng-invalid,
select.form-field.ng-touched.ng-invalid,
.select2-container.form-field.ng-touched.ng-invalid>a.select2-choice {
  border-color: #c64b49;
  background-color: #fff5f5;
}

.ui-menu-item > .ui-menu-item-wrapper {
  padding: 2px 4px;
}

ul.ui-autocomplete {
  z-index: 99999;
}

.form-control.readonly {
  background-color: #f6f6f6;
}

label.prepend-icon {
  margin-bottom: 0;
}

.image-box.image-field {
  width: 120px;

  input[type=file] {
    display: none;
  }
}

.data-panel.browsing {
  .image-box:hover .image-box-buttons {
      visibility: hidden;
  }
}

.data-panel.editing {
  .image-box:hover .image-box-buttons {
      visibility: visible;
  }
}

.data-heading {
  h2 {
    margin-top: 0;
    font-size: 1.3em;
    font-weight: 500;
  }
}

.ng-form.form-data-changing {
  .form-field-readonly {
    display: none;
  }
}

#katrid-action-view {
  height: 100%;
}

.content-scroll {
  height: 100%;
  width: 100%;
  display: flex;
  flex: 1 1 auto;
  position: relative;

  >.content {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
  }
}

.data-form {
  height: 100%;
  display: flex;
  flex-flow: column nowrap;

}

.data-heading {
  width: 100%;
  border: 1px solid #d6d6d6;
  >.panel-body {
    padding: 12px;
  }
}

.content {
  padding: 0;

  &.container {
    padding: 10px 0;
  }

  .data-panel {
    height: 100%;

    &.panel {
      border: 1px solid #c8c8d3;
      box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
      border-radius: 0;
    }

    .card-view {
      min-height: 100%;
    }
  }
}


.search-dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  list-style: none;
  font-size: 13px;
  text-align: left;
  background-color: #fff;
  border: 1px solid #ccc;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 2px;
  -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
  box-shadow: 0 6px 12px rgba(0,0,0,.175);
  background-clip: padding-box;

  >li>a {
    display: block;
    padding: 3px 20px;
    clear: both;
    font-weight: 400;
    line-height: 1.42857143;
    color: #333;
    white-space: nowrap;
  }
  >li.active>a {
    text-decoration: none;
    color: #fff;
    background-color: #3276b1;
  }
}

.search-view-filter-menu {
  li.selected {
    a {
      font-weight: bold;
    }

    a:before {
      font-family: FontAwesome;
      position: absolute;
      left: 6px;
      content: "\f00c";
    }
  }
}

div.form-data-changing {
  .form-field-readonly {
    display: none;
  }

  .form-label-checkbox span {
    display: none;
  }

  section[readonly] {
    .form-field-readonly {
      min-height: 26px;
      display: block;
      border: 1px solid #e6e6e6;
      background-color: #f5f5f5;
      padding: 3px 5px 0 8px;
      font-weight: normal;
      border-radius: @border-radius-base;
    }
    .form-field, .input-group, label.checkbox {
      display: none;
    }
    label.prepend-icon {
      display: none;
    }
  }
}

div.form-data-readonly {
  .form-field, .input-group {
    display: none;
  }
  label.prepend-icon {
    display: none;
  }
}

.list-record-selector {
  width: 1px;
}
